/**
 * Rem-converter mixin to convert px to rem
 */
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sup,
sub,
tt,
var,
b,
u,
i,
center,
ul,
ol,
li,
dl,
dt,
dd,
table,
tr,
th,
td,
thead,
tbody,
tfoot,
caption,
form,
fieldset,
legend,
input,
textarea,
select,
label,
applet,
object,
iframe,
audio,
video,
canvas,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary {
  font-size: 100%;
  font-family: inherit;
  font-weight: inherit;
  font-style: inherit;
  line-height: inherit;
  vertical-align: baseline;
  border: 0;
  outline: 0;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  text-size-adjust: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

audio:not([controls]) {
  display: none;
}

canvas {
  -ms-touch-action: double-tap-zoom;
}

[hidden] {
  display: none;
}

ul,
ol {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #363c31;
}
a:hover {
  color: #408647;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
}

body {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 16px;
  background: url(../assets/img/bg.svg) top center;
  color: #363c31;
  min-width: 320px;
  margin: 0 auto;
}

.wrapper {
  min-height: 100%;
  border-right: 1px solid #363c31;
  border-left: 1px solid #363c31;
  margin: 0 70px;
  position: relative;
}

.container {
  max-width: 100%;
  padding: 0 8.45%;
}

header {
  border-bottom: 1px solid #363c31;
}
header p {
  font-size: 40px;
  line-height: 54px;
  padding: 80px 0;
  text-align: center;
}

.logo_block {
  display: flex;
  gap: 118px;
  padding: 170px 0;
}
.logo_block .logo {
  flex: 0 0 250px;
  display: flex;
  align-items: center;
}
.logo_block .logo_text p {
  font-size: 64px;
  font-size: 4rem;
  line-height: 86px;
  font-weight: 400;
}

#about-gallery {
  display: flex;
  flex-wrap: wrap;
  --s: -15deg;
}
#about-gallery .wrapper {
  width: 100%;
}

.dateTime-content {
  display: -ms-flex;
  display: -webkit-flex;
  display: flex;
}

.about-img-wrapper {
  overflow: hidden;
  flex-grow: 1;
}

.about-img-wrapper > * {
  background: center/cover no-repeat;
  overflow: hidden;
  transform: skewX(var(--s, 0deg));
}

.about-img-wrapper > *::before {
  content: "";
  display: block;
  height: 100%;
  background: inherit;
  transform-origin: inherit;
  transform: skewX(calc(-1 * var(--s, 0deg)));
}

/**/
.image-1 {
  flex-basis: calc(383px + 495 * (100vw - 768px) / 1240);
  margin-right: calc(-220px + 20 * (100vw - 768px) / 1240);
  position: relative;
  left: -71px;
}

.image-1 > * {
  transform-origin: top;
  width: 100%;
  border-left: none;
  background-color: #408647;
}

.player_img {
  margin: calc(40px + 84 * (100vw - 480px) / 1440) calc(50px + 140 * (100vw - 480px) / 1440) calc(20px + 104 * (100vw - 480px) / 1440) calc(150px + 100 * (100vw - 480px) / 1440);
  transform: skewX(15deg);
}

/**/
.image-2 {
  flex-basis: calc(465px + 741 * (100vw - 768px) / 1152);
  margin-left: calc(0 + -130 * (100vw - 768px) / 1152);
  position: relative;
  top: calc(-10px + -24 * (100vw - 768px) / 1152);
  right: -70px;
}

.image-2 > * {
  transform-origin: bottom;
  border: 4px solid #408647;
  border-right: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.inner_content_block {
  height: 100%;
}

.inner_content {
  display: flex;
  flex-direction: column;
  transform: skewX(15deg);
  margin: calc(0px + 166 * (100vw - 480px) / 1440) calc(80px + 66 * (100vw - 480px) / 1440) calc(0px + 170 * (100vw - 480px) / 1440) 0;
}
.inner_content p {
  font-size: calc(40px + 28 * (100vw - 1000px) / 920);
  line-height: calc(56px + 39 * (100vw - 1000px) / 920);
  text-align: center;
  font-weight: 400;
}
.inner_content p.dateMonth {
  font-size: calc(100px + 120 * (100vw - 768px) / 1152);
  line-height: calc(120px + 144 * (100vw - 768px) / 1152);
  font-family: "Urbanist", sans-serif;
  font-weight: 300;
  letter-spacing: 0.01em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 4px;
  -webkit-text-stroke-color: #408647;
}

.contact_title {
  display: flex;
  gap: 303px;
  padding: 170px 0 100px;
  padding: calc(110px + 60 * (100vw - 768px) / 1152) 0 calc(60px + 110 * (100vw - 768px) / 1152);
  width: 100%;
}
.contact_title .contact_item p {
  font-size: 64px;
  font-size: 4rem;
  line-height: 90px;
  font-weight: 400;
}
.contact_title .contact_logo {
  flex: 0 0 213px;
  float: right;
}

.contact_map {
  display: grid;
  grid-template-columns: 1.3fr 2fr;
  grid-column-gap: 20px;
  min-height: 650px;
  padding-bottom: 148px;
}
.contact_map .address {
  position: relative;
}
.contact_map .address address {
  font-size: 40px;
  font-size: 2.5rem;
  line-height: 56px;
}
.contact_map .address address ul li {
  -moz-font-feature-settings: "lnum";
  -webkit-font-feature-settings: "lnum";
  font-feature-settings: "lnum";
}
.contact_map .address address p {
  margin-bottom: 20px;
}
.contact_map .address span {
  display: block;
  position: absolute;
  bottom: 0;
}
.contact_map .address span a {
  font-size: calc(16px + 8 * (100vw - 320px) / 1600);
  line-height: calc(22px + 10 * (100vw - 320px) / 1600);
  font-weight: 500;
}
.contact_map .contact_logo {
  display: none;
}

.content {
  max-width: 804px;
  margin: 0 auto;
  padding-top: calc(50px + 50 * (100vw - 320px) / 1600);
  padding-bottom: calc(146px + 129 * (100vw - 320px) / 1600);
}
.content section {
  margin-bottom: calc(50px + 20 * (100vw - 320px) / 1600);
}
.content section:last-child {
  margin-bottom: 0;
}
.content section h2 {
  margin-bottom: 20px;
  font-size: calc(20px + 12 * (100vw - 320px) / 1600);
  line-height: calc(28px + 17 * (100vw - 320px) / 1600);
}
.content section p,
.content section li {
  font-size: calc(15px + 5 * (100vw - 320px) / 1600);
  line-height: calc(21px + 7 * (100vw - 320px) / 1600);
  margin-bottom: 25px;
}
.content section p:last-child,
.content section li:last-child {
  margin-bottom: 0;
}

footer {
  border-top: 1px solid #363c31;
}
footer .footer_info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: calc(50px + 34 * (100vw - 1000px) / 920) calc(20px + 130 * (100vw - 1000px) / 920);
}
footer .footer_info .soc_link ul {
  display: flex;
  gap: 40px;
}
footer .footer_info .command_name a {
  font-size: calc(16px + 8 * (100vw - 320px) / 1600);
  line-height: calc(22px + 10 * (100vw - 320px) / 1600);
  font-weight: 500;
}

@media screen and (max-width: 1280px) {
  .wrapper {
    margin: 0 50px;
  }

  .container {
    padding: 0 calc(20px + 30 * (100vw - 320px) / 1600);
  }

  .logo_block {
    padding: calc(100px + 50 * (100vw - 320px) / 960) 0;
    gap: 102px;
  }
  .logo_block .logo {
    flex: 0 0 calc(212px + 48 * (100vw - 768px) / 512);
  }
  .logo_block .logo_text p {
    font-size: calc(48px + 16 * (100vw - 768px) / 512);
  }

  .contact_title {
    gap: 144px;
  }
  .contact_title .contact_item p {
    font-size: 64px;
    font-size: 4rem;
    line-height: 90px;
  }

  .contact_map {
    grid-template-columns: none;
    grid-template-rows: 1fr 3fr;
    grid-row-gap: 80px;
  }
  .contact_map .address {
    position: static;
  }
  .contact_map .address address {
    font-size: 40px;
    font-size: 2.5rem;
    line-height: 56px;
  }
  .contact_map .address span {
    left: 0;
    right: 0;
    width: 100%;
    text-align: center;
    bottom: calc(70px + 50 * (100vw - 320px) / 960);
  }
}
@media screen and (max-width: 1000px) {
  .wrapper {
    border-right: none;
    border-left: none;
    margin: 0;
  }

  .content {
    margin: 0 calc(20px + 80 * (100vw - 320px) / 680);
  }

  header p {
    font-size: 32px;
    font-size: 2rem;
    line-height: 43px;
  }

  .logo_block {
    gap: 90px;
  }
  .logo_block .logo_text p {
    line-height: 76px;
  }

  .contact_title {
    display: block;
  }
  .contact_title .contact_item p {
    font-size: 56px;
    font-size: 3.5rem;
    line-height: 78px;
  }
  .contact_title .contact_logo {
    display: none;
  }

  .contact_map .address {
    position: static;
    display: flex;
    justify-content: space-between;
  }
  .contact_map .address address {
    font-size: 32px;
    font-size: 2rem;
    line-height: 45px;
  }
  .contact_map .address .contact_logo {
    flex: 0 0 auto;
    display: block;
  }
  .contact_map .address .contact_logo img {
    float: right;
  }
  .contact_map #map {
    margin-bottom: calc(30px + 10 * (100vw - 320px) / 960);
  }

  footer .footer_info {
    padding: calc(50px + 0 * (100vw - 320px) / 680) calc(20px + 0 * (100vw - 320px) / 680);
  }
}
@media screen and (max-width: 768px) {
  header p {
    font-size: calc(16px + 16 * (100vw - 320px) / 448);
    padding: 50px 0;
  }

  .logo_block {
    gap: 60px;
    padding: calc(40px + 60 * (100vw - 320px) / 960) 0 calc(70px + 30 * (100vw - 320px) / 960);
  }
  .logo_block .logo_text p {
    line-height: 65px;
  }

  .contact_title .contact_item p {
    font-size: 52px;
    font-size: 3.25rem;
    line-height: 73px;
  }

  .contact_map .address address {
    font-size: 28px;
    font-size: 1.75rem;
    line-height: 40px;
  }

  footer .footer_info {
    flex-direction: column;
    align-items: center;
  }
  footer .footer_info .command_name {
    margin-bottom: 40px;
  }
}
@media screen and (max-width: 680px) {
  .logo_block {
    flex-wrap: wrap;
    gap: 40px;
  }
  .logo_block .logo {
    width: 100%;
    justify-content: center;
    flex: 0 0 auto;
  }
  .logo_block .logo_text p {
    text-align: center;
    line-height: 54px;
  }

  .contact_title {
    margin-top: 0;
  }
}
@media screen and (max-width: 480px) {
  header p {
    line-height: 32px;
    padding: 50px 0;
  }

  .logo_block .logo_text p {
    font-size: 40px;
    font-size: 2.5rem;
  }

  .contact_title {
    display: block;
  }
  .contact_title .contact_item p {
    font-size: 40px;
    font-size: 2.5rem;
    line-height: 54px;
  }
  .contact_title img {
    display: none;
  }

  .dateTime-content {
    flex-direction: column;
    overflow: hidden;
    padding: 0 calc(20px + 30 * (100vw - 320px) / 1600);
  }

  .image-1,
.image-2 {
    flex-basis: 100%;
    left: 0;
    right: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .image-2 {
    margin-top: calc(-115px + -15 * (100vw - 320px) / 160);
    border-bottom: 4px solid #408647;
    border-right: 4px solid #408647;
  }

  .about-img-wrapper {
    overflow: hidden;
    flex-grow: 1;
  }

  .about-img-wrapper > * {
    background: center/cover no-repeat;
    overflow: hidden;
    transform: skewY(var(--s, 0deg));
  }

  .about-img-wrapper > *::before {
    content: "";
    display: block;
    height: 100%;
    background: inherit;
    transform-origin: inherit;
    transform: skewY(calc(-1 * var(--s, 0deg)));
  }

  .image-1 > * {
    background-color: #408647;
    position: relative;
    top: -90px;
  }

  .player_img {
    margin: calc(140px + 20 * (100vw - 320px) / 160) calc(70px + 30 * (100vw - 320px) / 160) 60px calc(45px + 20 * (100vw - 320px) / 160);
    transform: skewY(11deg);
  }

  .image-2 > * {
    position: relative;
    top: calc(37px + 21 * (100vw - 320px) / 160);
    border-bottom: none;
  }

  .inner_content {
    transform: skewY(15deg);
    margin: calc(50px + 50 * (100vw - 320px) / 160) 0 calc(100px + 30 * (100vw - 320px) / 160);
  }
  .inner_content p {
    font-size: calc(20px + 12 * (100vw - 320px) / 160);
    line-height: calc(28px + 17 * (100vw - 320px) / 160);
  }
  .inner_content p.dateMonth {
    line-height: calc(72px + 48 * (100vw - 320px) / 160);
    font-size: calc(60px + 40 * (100vw - 320px) / 160);
    letter-spacing: 0.04em;
    -webkit-text-stroke-width: 3px;
  }

  .playDate {
    padding: 0 20px;
  }

  .contact_map {
    min-height: 500px;
  }
  .contact_map .address {
    width: 100%;
  }
  .contact_map .address address {
    font-size: 24px;
    font-size: 1.5rem;
    line-height: 33px;
  }
  .contact_map .contact_logo {
    display: none !important;
  }
}
@media screen and (max-width: 320px) {
  header p {
    line-height: 32px;
    padding: 30px 0;
  }

  .logo_block {
    gap: 40px;
  }
  .logo_block .logo_text p {
    font-size: 28px;
    font-size: 1.75rem;
    line-height: 38px;
  }

  .inner_content p.dateMonth {
    letter-spacing: 0.06em;
    -webkit-text-stroke-width: 2px;
    margin: 4px 0;
  }

  .contact_title {
    margin-top: 80px;
    padding-top: 0;
    padding-bottom: 50px;
  }
  .contact_title .contact_item p {
    font-size: 28px;
    font-size: 1.75rem;
    line-height: 38px;
  }

  .contact_map {
    grid-row-gap: 50px;
  }
  .contact_map .address p,
.contact_map .address ul li {
    font-size: 20px;
    font-size: 1.25rem;
    line-height: 28px;
  }
}